<template>
    <div>
        <h1>区块</h1>
        <MFilter></MFilter>
        <br>
        <div class="blocks">
            <Row :gutter="24">
                <Col span="8" v-for="(item, key) in blocks" :key="key">
                    <block-item :info="item" ></block-item>
                </Col>
            </Row>
            <Spin fix v-if="spinShow"></Spin>
        </div>
    </div>
</template>

<script>
import MFilter from '@/components/Filter';
import BlockItem from "./Item";

export default {
    name: "Blocks",
    data() {
        return {
            spinShow: false
        };
    },
    watch: {
        filterType: {
            handler: function(val) {
                this.search(val);
            },
            deep: true
        }
    },
    computed: {
        blocks: function() {
            return this.$store.state.blockMaterials;
        }
    },
    methods: {
    },
    components: {
        MFilter,
        BlockItem
    },
    mounted() {
        this.$store.dispatch('getBlocks');
    }
};
</script>

<style lang="less" scoped>
.el-row {
    margin-bottom: 24px;
}
.blocks {
    position: relative;
}
</style>
